<template><undefined>
<h1>Spin 加载中</h1>
<p>用于页面和区块的加载中状态。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<p>页面局部处于等待异步数据或正在渲染过程时，合适的加载动效会有效缓解用户的焦虑。</p>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;v-spin&gt;&lt;/v-spin&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><v-spin></v-spin>
</div>
                  <div slot="description"><h4 id="ji-ben"><a class="header-anchor" href="#ji-ben" aria-hidden="true">¶</a> 基本</h4>
<p>一个简单的 loading 状态</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-spin</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-spin</span>&gt;</span>
</code></pre>
</div>
                <summary>
  #### 基本
  一个简单的 loading 状态
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-spin</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-spin</span>&gt;</span>
</code></pre>
</demo-box>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;v-spin tip=\&quot;正在读取数据.\&quot;&gt;\n    &lt;v-alert type=\&quot;info\&quot; message=\&quot;消息提示的文案\&quot; description=\&quot;消息提示的辅助性文字介绍消息提示的辅助性文字介绍消息提示的辅助性文字介绍\&quot;&gt;&lt;/v-alert&gt;\n&lt;/v-spin&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><v-spin tip="正在读取数据.">
    <v-alert type="info" message="消息提示的文案" description="消息提示的辅助性文字介绍消息提示的辅助性文字介绍消息提示的辅助性文字介绍"></v-alert>
</v-spin>
</div>
                  <div slot="description"><h4 id="zi-ding-yi-miao-shu-wen-an"><a class="header-anchor" href="#zi-ding-yi-miao-shu-wen-an" aria-hidden="true">¶</a> 自定义描述文案</h4>
<p>自定义描述文案，指定的 tip 文案会直接代替 ...</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-spin</span> <span class="hljs-attr">tip</span>=<span class="hljs-string">"正在读取数据."</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span> <span class="hljs-attr">message</span>=<span class="hljs-string">"消息提示的文案"</span>
      <span class="hljs-attr">description</span>=<span class="hljs-string">"消息提示的辅助性文字介绍消息提示的辅助性文字介绍消息提示的辅助性文字介绍"</span>
    &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-alert</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">v-spin</span>&gt;</span>
</code></pre>
</div>
                <summary>
  #### 自定义描述文案
  自定义描述文案，指定的 tip 文案会直接代替 ...
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-spin</span> <span class="hljs-attr">tip</span>=<span class="hljs-string">"正在读取数据."</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span> <span class="hljs-attr">message</span>=<span class="hljs-string">"消息提示的文案"</span>
      <span class="hljs-attr">description</span>=<span class="hljs-string">"消息提示的辅助性文字介绍消息提示的辅助性文字介绍消息提示的辅助性文字介绍"</span>
    &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-alert</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">v-spin</span>&gt;</span>
</code></pre>
</demo-box>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;v-spin size=\&quot;small\&quot;&gt;&lt;/v-spin&gt;\n&lt;v-spin&gt;&lt;/v-spin&gt;\n&lt;v-spin size=\&quot;large\&quot;&gt;&lt;/v-spin&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><v-spin size="small"></v-spin>
<v-spin></v-spin>
<v-spin size="large"></v-spin>
</div>
                  <div slot="description"><h4 id="ge-chong-da-xiao"><a class="header-anchor" href="#ge-chong-da-xiao" aria-hidden="true">¶</a> 各种大小</h4>
<p>小的用于文本加载，默认用于卡片容器级加载，大的用于页面级加载。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-spin</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-spin</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">v-spin</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-spin</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">v-spin</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"large"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-spin</span>&gt;</span>
</code></pre>
</div>
                <summary>
  #### 各种大小
  小的用于文本加载，默认用于卡片容器级加载，大的用于页面级加载。
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-spin</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-spin</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">v-spin</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-spin</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">v-spin</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"large"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-spin</span>&gt;</span>
</code></pre>
</demo-box>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-spin :spinning=\&quot;spinning\&quot; tip=\&quot;加载中\&quot;&gt;\n        &lt;v-alert type=\&quot;info\&quot; message=\&quot;消息提示的文案\&quot; description=\&quot;消息提示的辅助性文字介绍消息提示的辅助性文字介绍消息提示的辅助性文字介绍\&quot;&gt;\n        &lt;/v-alert&gt;\n    &lt;/v-spin&gt;\n    切换加载状态：&lt;v-switch v-model=\&quot;spinning\&quot;&gt;&lt;/v-switch&gt;\n&lt;/template&gt;\n&quot;,&quot;script&quot;:&quot;\nexport default {\n   data: ()=&gt; ({\n       spinning:false\n   })\n}\n&lt;script&gt;\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <v-spin :spinning="spinning" tip="加载中">
        <v-alert type="info" message="消息提示的文案" description="消息提示的辅助性文字介绍消息提示的辅助性文字介绍消息提示的辅助性文字介绍">
        </v-alert>
    </v-spin>
    切换加载状态：<v-switch v-model="spinning"></v-switch>
</template>
</div>
                  <div slot="description"><h4 id="qia-pian-jia-zai-zhong"><a class="header-anchor" href="#qia-pian-jia-zai-zhong" aria-hidden="true">¶</a> 卡片加载中</h4>
<p>可以直接把内容内嵌到 Spin 中，将现有容器变为加载状态。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-spin</span> <span class="hljs-attr">:spinning</span>=<span class="hljs-string">"spinning"</span> <span class="hljs-attr">tip</span>=<span class="hljs-string">"加载中"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span> <span class="hljs-attr">message</span>=<span class="hljs-string">"消息提示的文案"</span> <span class="hljs-attr">description</span>=<span class="hljs-string">"消息提示的辅助性文字介绍消息提示的辅助性文字介绍消息提示的辅助性文字介绍"</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">v-alert</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">v-spin</span>&gt;</span>
    切换加载状态：<span class="hljs-tag">&lt;<span class="hljs-name">v-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"spinning"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-switch</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
   <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-params">()</span>=&gt;</span> ({
       <span class="hljs-attr">spinning</span>:<span class="hljs-literal">false</span>
   })
}
&lt;script&gt;
</span></code></pre>
</div>
                <summary>
  #### 卡片加载中
  可以直接把内容内嵌到 Spin 中，将现有容器变为加载状态。
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-spin</span> <span class="hljs-attr">:spinning</span>=<span class="hljs-string">"spinning"</span> <span class="hljs-attr">tip</span>=<span class="hljs-string">"加载中"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span> <span class="hljs-attr">message</span>=<span class="hljs-string">"消息提示的文案"</span> <span class="hljs-attr">description</span>=<span class="hljs-string">"消息提示的辅助性文字介绍消息提示的辅助性文字介绍消息提示的辅助性文字介绍"</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">v-alert</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">v-spin</span>&gt;</span>
    切换加载状态：<span class="hljs-tag">&lt;<span class="hljs-name">v-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"spinning"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-switch</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
   <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-params">()</span>=&gt;</span> ({
       <span class="hljs-attr">spinning</span>:<span class="hljs-literal">false</span>
   })
}
&lt;script&gt;
</span></code></pre>
</demo-box>
<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="spin-props"><a class="header-anchor" href="#spin-props" aria-hidden="true">¶</a> Spin Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>size</td>
<td>spin组件中点的大小，可选值为 small default large</td>
<td>string</td>
<td>default</td>
</tr>
<tr>
<td>spinning</td>
<td>用于内嵌其他组件的模式，可以关闭 loading 效果</td>
<td>boolean</td>
<td>true</td>
</tr>
<tr>
<td>tip</td>
<td>自定义描述文案</td>
<td>string</td>
<td>无</td>
</tr>
</tbody>
</table>
</undefined></template>

<script>
export default {
    data:()=> ({
      spinning:false
    })
}
</script>


